<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
// 

export default {
  name: 'App',
  created() {
    // createdmounted
    if (process.env.NODE_ENV === 'production') {
      const isVerified = localStorage.getItem('passwordVerified') === 'true';
      if (!isVerified && this.$route.path !== '/password') {
        this.$router.push('/password');
      } else if (isVerified && this.$route.path === '/password') {
        // 
        this.$router.push('/');
      }
    }
  },
  mounted() {
    // mounted
    if (process.env.NODE_ENV === 'production') {
      const isVerified = localStorage.getItem('passwordVerified') === 'true';
      if (!isVerified && this.$route.path !== '/password') {
        this.$router.push('/password');
      } else if (isVerified && this.$route.path === '/password') {
        this.$router.push('/');
      }
    }
  }
}
</script>
